.error-wrapper
{
    width: 100%;
    height: 98vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    color: white;
}

.error-details
{
    display: flex;
    margin: 20px;
    text-align: center;
}

.error-code
{
    margin-right: 10px;
    border-right: 0.4px solid rgb(214, 214, 214);
    padding-right: 10px;
}