
html
{
    scroll-behavior: smooth;
}

.dash-wrapper
{
    width: 100%;
    height: 95vh;
    color: rgb(243, 243, 243);
    text-align: center;
}

.dash-headings{
    display: flex;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
}

.dash-headings img{
    width: 100px;
    height: 90px;
    border-radius: 10px;
    box-shadow: 0 0 30px rgb(155, 155, 155);
    
}

.user-profiles{
    position: absolute;
    width: 200;
    top: 5px;
    right: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

@media screen and (max-width: 1184px) {
    .user-profiles{
        position: relative;
        float: right;
    }
}

.user-profiles img{
    cursor: pointer;
}

.table-list{
    margin: 30px 100px;
    display: flex;
    flex-direction: column;
}

.top-row{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.top-row div
{
    color: rgb(153, 153, 153);
    background-color: white;
    border-radius: 10px;
    margin: 10px 10px;
    min-width: 240px;
    border: none;
    min-height: 210px;
    display: flex;
    flex-direction: column;
    text-align: center;
    box-shadow: 0 0 30px rgb(131, 131, 131);
}

.bottom-row{
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin: 20px;
    flex-wrap: wrap;
}

::-webkit-scrollbar
{
    width: 10px;
    background-color: rgb(0, 138, 202);
}

::-webkit-scrollbar-thumb
{
    border-radius: 50px;
    background: linear-gradient(transparent, rgb(2, 196, 255));
}

::-webkit-scrollbar-thumb:hover
{
    background: linear-gradient(transparent, rgb(3, 121, 255));
}

.archive-bottom-row
{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
    border-top: 1px solid rgb(255, 255, 255);
}