.login-wrapper
{
    width: 100%;
    height: 98vh;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    color: rgb(175, 174, 174);
    justify-content: center;
}

.login-container
{
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 0 20px grey;
    height: 500px;
    width: 480px;
}

.images-msg
{
    margin: 40px;
    display: flex;
}

.images-msg img{
    width: 80px;
    height: 70px;
    margin-left: 10px;
}