body{
  background: linear-gradient(rgb(43, 226, 250), rgb(2, 155, 202)) center center no-repeat fixed;
  background-size: cover;
}

.wrapper
{
  width: 100%;
  height: 95vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}

.table-wrapper
{
    box-shadow: 0 0 30px rgb(87, 87, 87);
    text-align: center;
    margin-bottom: 10px;
    min-width: 100%;
    border-collapse: collapse;
}

.table-wrapper table{
    background-color: white;
    border-collapse: collapse;
    min-width: 100vw;
}

.table-wrapper table thead th{
    position: sticky;
    background-color: rgb(15, 145, 221);
    height: 40px;
    color: white;
    top: 0;
    z-index: 100;
    font-size: 1rem;
}

.table-wrapper table td{
    border: 0.1px solid rgb(223, 223, 223);
    font-size: 0.8rem;
}

.heading
{
  color: rgb(240, 240, 240);
}

.cargo_others_data_inputs
{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-right: 20px;
  width: 47%;
  border: 0.3px solid rgb(207, 207, 207);
  border-radius: 10px;
}

.suitable_vessel_inputs
{
  width: 47%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-right: 20px;
  border: 0.3px solid rgb(207, 207, 207);
  border-radius: 10px;
}

.footer
{
  bottom: 0;
  margin-top: 10px;
  color: white;
  width: 100%;
  display: flex;
  justify-content: center;
}

.footer img{
  width: 30px;
  height: 40px;
  margin-right: 20px;
  margin-left: 20px;
}